.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

.modal {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.5);
  display: flex;
  justify-content: center;
  align-items: center;
}

.store {
  background-color: #b69222;
  padding: 20px;
  border-radius: 5px;
}

.store-icon {
  width: 50px;
  cursor: pointer;
  display: block;
  margin: 0 auto; 
  margin-top: 20px;
  margin-bottom: 20px;
}

.robot-icon {
  width: 80px; /* Ajusta el ancho como desees */
  height: auto; /* Mantiene la relación de aspecto */
  cursor: pointer; /* Cambia el cursor a una mano cuando pasa sobre la imagen */
}

.robot-icon.disabled {
  filter: grayscale(100%); /* Hace la imagen completamente gris */
  opacity: 80%;
}

.robot-cost {
  text-align: center;
  margin-top: 10px; /* Ajusta según tus necesidades */
}

.robot-level {
  text-align: center;
  margin-bottom: 10px; /* Espacio adicional antes de la imagen del robot */
}

.close-icon {
  width: 30px; /* Ajusta el ancho como desees */
  height: auto; /* Mantiene la relación de aspecto */
  cursor: pointer; /* Cambia el cursor a una mano cuando pasa sobre la imagen */
  display: block; /* Asegura que la imagen sea un bloque para que pueda tener margen */
  margin: 10px auto; /* Centra la imagen horizontalmente y añade margen */
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
