.energy-bar {
    position: relative;
    width: 100%;
    height: 24px; /* Ajusta según el tamaño deseado de la barra */
    background-color: #ddd;
    margin: 10px 0;
    border-radius: 20px;
  }
  
  .energy-fill {
    height: 24px;
    background-color: rgb(255, 81, 0);
    transition: width 0.5s ease-in-out;
    border-radius: 20px;
  }

  
  .energy-icon {
    height: 70%; /* El icono se adapta al tamaño de la barra */
    position: absolute;
    left: 50%;
    transform: translateX(-50%) translateY(25%); /* Centra horizontalmente el ícono */
  }
  